<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="wareHouseVisible"
      width="700px"
      @close="clearWareHouseInfo"
    >
      <div class="head-tool">
        <div class="warehouseNum">{{warehouseInfo.wwNo}}</div>
        <el-autocomplete
          class="selectSearch"
          v-model="state"
          :fetch-suggestions="querySearchAsync"
          placeholder=""
          @select="handleSelect"
        ></el-autocomplete>
        <el-button
          class="addWarehouse"
          type="primary"
          @click="deleteRecord('')"
        ></el-button>
        <el-button
          class="addWarehouse"
          type="primary"
          @click="warehouseInnerVisible = true"
        ></el-button>
      </div>
      <div class="dialog-info">
        <span class="dialog-name"></span>
        <span class="dialog-company">{{wareHouseList.customerName}}</span>
      </div>
      <el-form
        :model="warehouseInfo"
        style="margin-left:10px"
        :rules="rules"
      >
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.goodsName}}
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.goodsName}}
        </el-form-item>
        <el-form-item
          label="/"
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.brand}}
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.qualityStandard}}
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.packingSpecification}}
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.quantity}}
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          {{warehouseInfo.unit}}
        </el-form-item>
      </el-form>
      <el-dialog
        title=""
        :visible.sync="warehouseInnerVisible"
        width="1200px"
        append-to-body
        @close="clearAddWareHouseTable"
      >
        <div class="tableDate">
          <div
            class="button"
            style="width:3%;float:right;"
          >
            <P>
              <el-button
                class="el-icon-plus"
                @click.prevent="addRow()"
              ></el-button>
            </P>
            <p>
              <el-button
                class="el-icon-minus"
                @click.prevent="delData()"
              ></el-button>
            </p>
          </div>
          <div class="table">
            <el-table
              :data="addWareHouseTable"
              ref="addWareHouseTable"
              tooltip-effect="dark"
              border
              stripe
              style="width: 95%"
              @selection-change="selectRow"
            >
              <el-table-column
                type="selection"
                width="45"
                align="center"
              ></el-table-column>
              <el-table-column
                label=""
                align="center"
                width="195"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.wwNo"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="90"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsName"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="/"
                width="100"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.brand"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="110"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.qualityStandard"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.packingSpecification"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="105"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.quantity"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="80"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.unit"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.warehouseName"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="205"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.warehouseAddress"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.linkman"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="165"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.linkPhone"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="165"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsAddress"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label=""
                width="115"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsAuthenticity"></el-input>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div
          slot="footer"
          class="dialog-footer"
        >
          <el-button
            type="primary"
            @click="submitWarehouse"
          > </el-button>
        </div>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../../../validate.js";
import * as ajaxUrl from "../../../ajax.config.js";

export default {
  props: ["warehouseVisible", "wareHouseInfo", "warehouseList"],
  data() {
    return {
      state: "",
      rules: rules,
      formLabelWidth: "135px",
      wareHouseVisible: this.warehouseVisible,
      warehouseInnerVisible: false,
      addWareHouseTable: [],
      warehouseInfo: {},
      wareHouseList: [],
    };
  },
  watch: {
    warehouseVisible(val) {
      this.wareHouseVisible = val; //resultwatchmyResult
    },
    wareHouseVisible(val) {
      this.$emit("wareHouseVisibleChange", val); //dialogFormVisibleChange
    },
    wareHouseInfo(val) {
      this.warehouseInfo = val;
    },
    warehouseList(val) {
      this.wareHouseList = val; 
    },
  },
  methods: {
    // 
    querySearchAsync(queryString, cb) {
      let wareHouseList = this.wareHouseList;
      for (let i of wareHouseList) {
        i.value = i.wwNo; //value
      }
      let results = queryString
        ? wareHouseList.filter(this.createStateFilter(queryString))
        : wareHouseList;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 300 * Math.random());
    },
    handleSelect(item) {
      this.warehouseInfo = item;
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    clearAddWareHouseTable() {
      this.addWareHouseTable = [];
    },
    // 
    selectRow(val) {
      this.selectlistRow = val;
    },
    // 
    addRow() {
      let list = {
        rowNum: this.addWareHouseTable.length + 1,
        address: this.address,
        name: this.name,
        weather: this.weather,
        phone: this.phone,
        date: this.date,
        mdate: this.mdate,
        loveer: this.loveer
      };
      this.addWareHouseTable.unshift(list);
    },
    // 
    // 
    delData() {
      if (this.selectlistRow) {
        for (let i = 0; i < this.selectlistRow.length; i++) {
          let val = this.selectlistRow;
          // 
          // tableDatavalrowNum,
          // rowNum
          val.forEach((val, index) => {
            this.addWareHouseTable.forEach((v, i) => {
              if (val.rowNum === v.rowNum) {
                // i 
                this.addWareHouseTable.splice(i, 1);
              }
            });
          });
        }
      }
      // 
      this.$refs.addWareHouseTable.clearSelection();
    },
    submitWarehouse() {
      let params = {
        id: this.wareHouseInfo.contractId,
        warehouseWarrantList: this.addWareHouseTable
      };
      this.axios
        .post(ajaxUrl.BATCHWAREHOUSEWARRANT, params)
        .then(res => {
          if (res.data.resCode == 200) {
            this.warehouseInnerVisible = false;
            this.wareHouseVisible = false;
            this.$emit("searchInfo");
            this.addWareHouseTable = [];
          } else {
            this.$message.error(res.data.resMsg);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    clearWareHouseInfo() {
      this.warehouseInfo = {};
      this.state = "";
    },
    //
    deleteRecord(type) {
      if (type == "") {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.axios.delete(ajaxUrl.GETWAREHOUSEWARRANT+"/"+this.warehouseInfo.id)
            .then(res => {
              if(res.data.resCode == 200){
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.wareHouseVisible = false;
                this.$router.push("/GetContract");
                this.warehouseInfo = {},
                this.$emit("searchInfo");
              }else{
                this.$message.error(res.data.resMsg)
              }
            })
            .catch(err => {
              console.log(err);
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
  },
  components: {}
};
</script>

<style scoped>
.warehouseNum {
  width: 110px;
}
</style>



